import React from 'react';

import { Button, Form, Grid, Header, Card, Icon } from 'semantic-ui-react';

const Auth = (props) => {
  return (
    <Grid centered style={{ height: '100vh' }} verticalAlign="middle">
      <Grid.Column style={{ maxWidth: 400 }}>
        <Card style={{ width: '100%' }}>
          <Card.Content>
            <Card.Header>
              <Icon name="lock" />
              Autenticação
            </Card.Header>
            <Header as="h2" textAlign="center">
              Faça login em sua conta
            </Header>
            <Form>
              <Form.Input size="small" fluid icon="user" iconPosition="left" placeholder="E-mail address" />
              {/* <Form.Input size="small" fluid icon="lock" iconPosition="left" placeholder="Password" type="password" /> */}

              <Button color="blue" fluid>
                Login
              </Button>
            </Form>
          </Card.Content>
        </Card>
        <div style={{ textAlign: 'right' }}>Não tem conta? Basta inserir seu email</div>
      </Grid.Column>
    </Grid>
  );
};

export default Auth;
