import React from 'react';
import { render } from 'react-dom';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';

import utility from '@ciro-maciel/utility';

import * as containers from 'containers';

import 'semantic-ui-css/semantic.min.css';
import 'tui-calendar/dist/tui-calendar.css';

const Index = () => {
  utility.net.info.load();

  return (
    <>
      <Route exact path="/auth" component={containers.Auth} />
      <Route exact path="/on" component={containers.Home.On} />
      <Route exact path="/off" component={containers.Home.Off} />
      <Route exact path="/recipe" component={containers.Recipe} />
      <Route exact path="/recipes" component={containers.Recipes} />
      <Route exact path="/profile" component={containers.Profile} />
      <Route exact path="/planner" component={containers.Planner} />
      <Redirect to="/off" />
    </>
  );

  // return (
  //   <AppProvider client={client}>
  //     <Routes />
  //   </AppProvider>
  // );
};

render(
  <BrowserRouter>
    <Index />
  </BrowserRouter>,
  document.getElementById('container')
);
